import React, { useState, useEffect } from 'react';
import Axios from 'axios';


// Marteial Ui Imports
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import EditRoundedIcon from '@material-ui/icons/EditRounded';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import Modal from '@material-ui/core/Modal';


import './updateCostumer.css'

import { CSVLink } from "react-csv";
const proxy = 'http://localhost:8000/';
const serverURL = `${proxy}getcostumer/`
const updateCostumerURL = `${proxy}updatecostumer`
const deletecostumerServer = `${proxy}deletecostumer`

const AccessToken = sessionStorage.getItem("access_token")

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

export default function UpdateCostumer() {
    const [costumerInput, setCostumerinput] = useState({
        name: "",
        loanammount: "",
        loan_date: "",
        loanInterst: "",
        ms : "",
        place: "",
        place_id: ""
    })
    const [costumerDetails, setCostumerDetails] = useState([{
        name: "",
        loanammount: "",
        loan_date: "",
        loanInterst: "",
        ms : "",
        place: "",
        place_id: ""
    }])
    const [searchTerm, setSearchterm] = useState('');
    const [editCostumerId, setEditCostumerId] = useState(null);
    const [open, setOpen] = React.useState(false); 
    const [modalopen, setModalOpen] = React.useState(false); 

    const handleSubmit = (e) => {
        e.preventDefault()
        if (editCostumerId != null) {
            updateCostumer(costumerInput)
        }
    }

    const clearinputs = () => {
        setCostumerinput({
            name: "",
            loanammount: "",
            ammount_Balance: "",
            loan_date: "",
            loanInterst: "",
            ms: "",
            place: "",
            place_id: ""
        });
        setSearchterm('');
    }

    const updateCostumer = async(costumerdata) => {
        const url = `${updateCostumerURL}/${editCostumerId}/`;
        Axios.post(url, costumerdata).then((result) => {
            setEditCostumerId(null)
            getcostumers();
            setModalOpen(false)
            clearinputs();
        }, (error) => {
            console.log(error, 'error')
        })
    }

    const getcostumers = () => {
        const URL = `${serverURL}${AccessToken}/`
        Axios.get(URL).then((result) => {
                setCostumerDetails(result.data)
        }, (error) => {
            alert('Error Loading Data')
        })
        console.log(costumerDetails)
    }

    const handlechange = (e) => {
            setCostumerinput({
                ...costumerInput,
                [e.target.name]: e.target.value
            })
    }

    const editcostumerdata = (costumerData) => {
        const { id, name, loanammount, ammount_Balance, loan_date, loanInterst, ms, place, place_id } = costumerData
        setEditCostumerId(id);
        setCostumerinput({
            name, loanammount, ammount_Balance, loan_date, loanInterst, ms, place, place_id
        });
        setModalOpen(true)
    };

    const deletecostumer = async(costumerInfo) => {
        const url = `${deletecostumerServer}/${costumerInfo.id}/${AccessToken}/`
        await Axios.delete(url).then((result) => {
            setOpen(true)
            getcostumers();
            clearinputs()
        }, (error) => {
            console.log(error, 'error')
        });
        window.location.reload();
    }

    const searchCostumer = (e) => {
        setSearchterm(e.target.value);
    }

    useEffect(() => {
        getcostumers()
    }, [])

    const handleClose = () => {
        setOpen(false);
    };

    const headers = [
                { label: "Name"},
                { label: "Balance"},
                { label: "Loan Date"},
                { label: "Day1"},
                { label: "Day2"},
                { label: "Day3"},
            ];
        const data = costumerDetails.map(data => [data.name, data.ammount_Balance, data.loan_date]);
        let content = {
            data: data,
            headers: headers,
            filename: `${new Date().toDateString()}_finData.csv`
        };
        const searchdata =  costumerDetails.filter((details) => 
                                     details.name.includes(searchTerm)
                                ).map((data, index) => {
                                    return (
                                        <tr className="updateCostumers_tb_tb" key={index}>
                                            <td>{data.name}</td>
                                            <td>{data.place}</td>
                                            <td>{data.loanammount}</td>
                                            <td>{data.ammount_Balance}</td>
                                            <td>{data.loan_date}</td>
                                            <td>{data.loanInterst}</td>
                                            <td style={{"padding" : "12"}}>{data.ms}</td>
                                            <td>
                                                <Button
                                                    color="primary"
                                                    style={{"outline" : "none"}}
                                                    onClick={() => editcostumerdata(data)} >
                                                <EditRoundedIcon />
                                                </Button>
                                            </td>
                                            <td>
                                                <Button
                                                    color="secondary"
                                                    style={{"outline" : "none"}}
                                                    onClick={() => deletecostumer(data)} >
                                                <DeleteForeverIcon />
                                                </Button>
                                            </td>
                                        </tr>
                                    )
                                })

    return (
        <div className="updateCostumer">
            <Modal
                open={modalopen}
                onClose={handleClose}
                style={{
                    "color": "black",
                    "height": "70%",
                    "display": 'flex',
                    "alignItems": 'center',
                    "justifyContent": 'center',
                    "margin-top" : 70
                }}
            >
                <div className="updateCostumer__part1">
                    <h4>Update Costumer</h4>
                        <input type="text"  onChange={handlechange}  value={costumerInput.name} placeholder="Costumer Name" name="name" />
                        <input type="text"  onChange={handlechange}  value={costumerInput.loanammount} placeholder="Loan Ammount" name="loanammount" />
                        <input type="text"  onChange={handlechange}  value={costumerInput.ammount_Balance} placeholder="Ammount Balance" name="ammount_Balance" />
                        <TextField
                            type="date"
                            name="loan_date"
                            InputLabelProps={{
                                shrink: true,
                        }}
                            defaultValue={costumerInput.loan_date}
                            onChange={handlechange}
                    />
                        <input type="text" onChange={handlechange} value={costumerInput.loanInterst} placeholder="Loan Intrest" name="loanIntrest" />
                        <input type="text" onChange={handlechange}  value={costumerInput.ms} placeholder="MS" name="ms" />
                        <input type="text" onChange={handlechange}  value={costumerInput.place} placeholder="Place" name="place" />
                        <input type="text" onChange={handlechange} value={costumerInput.place_id} placeholder="Place Id" name="place_id" />
                        <Button variant="outlined" style={{"outline" : "none"}} color="primary" onClick={ handleSubmit} >Update Costumer</Button>
                </div>
            </Modal>
                <div className="updateCostumer__part2">
                    <TextField id="outlined-search" onChange={searchCostumer} className="updateCostumers_searchbutton"  label="Enter Costumer Name" type="search" variant="outlined" />
                <table className="updateCostumers_table">
                    <thead className="updateCostumers_th">
                        <tr>
                            <td>Name</td>
                            <td>Place</td>
                            <td>Loan Ammount</td>
                            <td>Ammount Balance</td>
                            <td>Loan Date</td>
                            <td>Loan Intrest</td>
                            <td>MS</td>
                            <td>Edit</td>
                            <td>Delete</td>

                        </tr>
                    </thead>
                    <tbody className="updateCostumers_tb">
                            {
                                searchdata
                            }
                    </tbody>
                </table>
                </div>
            <CSVLink {...content} style={{ height: '35px', border: 'none', padding: '4px' }}>Export to CSV</CSVLink>
                 <Snackbar open={open} autoHideDuration={6000}>
                    <Alert onClose={handleClose} severity="success">
                        Costumer Deleted Successfully
                    </Alert>
            </Snackbar>
            </div>
    )
}

