import React, { Component } from 'react';
import axios from 'axios';
import './ccccollection.css';
const proxy = "http://localhost:8000/";
const authKey = sessionStorage.getItem('access_token');
const CollectionList_Server = `${proxy}getcollection/${authKey}/`
const costumerServer = `${proxy}getcostumer/${authKey}/`

export default class Cotumercollection extends Component {
    constructor(props) {
        super(props);
        this.state = {
            date1 : '',
            searchTerm: '',
            collectionData: [],
        }
    }

    changeDate = async e => {
        await this.setState({
            date1 : e.target.value
        })
    }

    setsearchCostumer = (e) => {
        this.setState({
            searchTerm: e.target.value
        })
    }

    searchCostumerData = async () => {
        let collections;
        let costumerData;
        let loanDate;
        await axios.get(costumerServer).then((res) => {
            costumerData = res.data.filter((searchCostumer) => searchCostumer.name.toLowerCase().includes(this.state.searchTerm))
        })
        await costumerData.map((cd) => {
            return loanDate = cd.loan_date
        })
        loanDate = new Date(loanDate)
        loanDate = loanDate.toDateString()
        await axios.get(CollectionList_Server).then((res) => {
            return collections = res.data
        })
        var searchdata =  collections.filter((collectionlist) => 
                                    collectionlist.costumer_name.toLowerCase().includes(this.state.searchTerm.toLowerCase())
        )
        let exactData = searchdata.filter((data1) => data1.date < loanDate)
        this.setState({
            collectionData : exactData
        })
    }

    getCostumerdata = async () => {
        const searchidid = await sessionStorage.getItem('getcollection');
        console.log(searchidid)
        if (searchidid !== null) {
            let collections = [];
            await axios.get(CollectionList_Server).then((res) => {
                return collections = res.data
            })

            let data = [];

            collections.map((data11) => {
                if(data11.costumer_id.toString() === searchidid.toString()) {
                    data.push(data11)
                }
            })
            this.setState({
                collectionData: data
            })
            console.log(this.state.collectionData,"forigin praveen")
        }
    };

    componentDidMount() {
        this.getCostumerdata();
    }

    render() {

        return (
            <div className="collectionDetails__">
                <div className="collectionDetails__header">
                    <input type="text" id="outlined-search"
                        style={{
                            marginLeft: '20px',
                            outline: 'none',
                            flex: '0.9'
                        }}
                        onChange={this.setsearchCostumer} placeholder="Enter Costumer Name"
                        className="collectionDetails__input"
                    />
                    <button className="collectionDetails__button" onClick={this.searchCostumerData}>🔍 Search</button>
                    </div>
                <div className="collectionDetails__body">
                    <div className="collectionDetails__collectionTable1">
                        <table>
                            <thead>
                                <tr>
                                    <th>Date</th>
                                    {
                                         this.state.searchTerm === "" && ( <th className="collection_name_">Costumer Name</th> )
                                    }
                                    <th>Ammount Collected</th>
                                </tr>
                            </thead>
                            <tbody className="collectionDetails__collectiontr">
                                {
                                    this.state.searchTerm === "" && (
                                        this.state.collectionData.map((data) => {
                                            return (
                                                <tr key={data.id}>
                                                    <td>{data.date}</td>
                                                    <td className="collection_name_">{data.costumer_name}</td>
                                                    <td>{data.ammount}</td>
                                                </tr>
                                            )
                                        })
                                    )
                                }
                                 {
                                    this.state.searchTerm !== "" && (
                                        this.state.collectionData.map((data) => {
                                            return (
                                                <tr key={data.id}>
                                                    <td>{data.date}</td>
                                                    <td>{data.ammount}</td>
                                                </tr>
                                            )
                                        })
                                    )
                                }
                           
                        </tbody>
                    </table>
                </div>
                </div>
            </div>
        )
    }
}
