import React, { Component } from 'react';
import axios from 'axios';

const proxy = 'http://localhost:8000/';
const Expence_server = `${proxy}createexpence/`;

class expences extends Component {
	constructor(props) {
		super(props);
		this.state = {
			date1: '',
			expenceName: '',
			expenceAmmount: '',
		};
	}

	submitExpence = () => {
		let dailyExpence = {
			date: this.state.date1,
			expenceName: this.state.expenceName,
			expenceAmmount: this.state.expenceAmmount,
		};
		axios.post(Expence_server, dailyExpence);
		this.clearData();
	};

	clearData = () => {
		this.setState({
			expenceName: '',
			expenceAmmount: '',
		});
	};

	getCurrentDate = async () => {
		let date1 = new Date();
		let month = date1.getMonth();
		let date = date1.getDate();
		if (month < 10) {
			month = parseInt(month) + 1;
			month = '0' + month;
		}
		if (date < 10) {
			date = parseInt(date);
			date = '0' + date;
		}
		let prevDate = date1.getFullYear() + '-' + month + '-' + date;
		await this.setState({
			date1: prevDate,
		});
	};

	setval = async (e) => {
		this.setState({
			expenceAmmount: e.target.value,
		});
	};

	componentDidMount() {
		this.getCurrentDate();
		this.setState({
			dailyExpence: {
				...this.state.dailyExpence,
				date: this.state.date1,
			},
		});
	}

	render() {
		return (
			<div>
				<div className='reports__expence'>
					<a
						style={{
							'width': '200px',
							'margin': '0px 0px 20px 0px',
							'padding': '10px',
							'border': 'solid thin black',
							'text-align': 'center',
							'textDecoration': 'none',
						}}
						href='/reports'>
						Back
					</a>
					<h3 style={{ 'font-size': '30px' }}>Expence</h3>
					<h3>Date :{this.state.date1}</h3>
					<div
						style={{
							display: 'flex',
							flexDirection: 'column',
							height: '200px',
							justifyContent: 'space-evenly',
						}}>
						Expence Name:{' '}
						<select
							name='expenceName'
							onChange={(e) =>
								this.setState({
									expenceName: e.target.value,
								})
							}>
							<option>லைன் செலவு</option>
							<option>டீ</option>
							<option>சாப்பாடு</option>
							<option>பெட்ரோல்</option>
							<option>தின் பண்டம்</option>
							<option>மருத்துவ செலவு</option>
							<option>வண்டி செலவு</option>
							<option>வண்டி ஆயில் மாத்தியது</option>
							<option>வண்டி டையர் மாத்தியது</option>
							<option>வண்டி செயின்</option>
							<option>மற்ற செலவுகள்</option>
						</select>
						<input
							type='text'
							placeholder='Ammount'
							onChange={this.setval}
							value={this.state.expenceAmmount}></input>
						<button onClick={this.submitExpence}>Add Expence 💾</button>
					</div>
				</div>
			</div>
		);
	}
}

export default expences;
