import React, { useState, useEffect, useRef } from 'react'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import axios from 'axios'
import "./addCollection.css"


const authKey = sessionStorage.getItem("access_token");
const proxy = "http://localhost:8000/"
const CostumerDetails_Server = `${proxy}getcostumer/${authKey}/`;
const CollectionList_Server = `${proxy}getcollection/${authKey}/`;
const updateCostumerDetails_Server = `${proxy}updatecostumer/`;
const CollectionListDetail_Server = `${proxy}getcollectiondetail/`;
const createCollectionList_Server = `${proxy}createcollection/${authKey}/`;

const clamp = (min, max, val) => Math.max(min, Math.min(val, max));

export default function AddCollection() {
    const [loadingState, setLoadingState] = useState(true);
    const [collectedammout, setCollectedammount] = useState("");
    const [costumerDetails, setCostumerDetails] = useState([{
        name: '',
        loanammount: '',
        loan_date: '',
        loanInterst: '',
        ms: '',
        place: '',
        ammount_Balance: 'updated_balance',
        place_id: '',
    }])
    const [date1, setDate1] = useState('');
    const [searchTerm, setSearchTerm] = useState('');
    const [collectionData, setCollectionData] = useState([{
        
    }]);
    const inputRefs = useRef([]);
    const [collectionStateData, setcollectionStateData] = useState('');

    const changeDate = async e => {
        setDate1(e.target.value)
    };

    const changevalue = (e) => {
        setCollectedammount(e.target.value)
    };
   
    const changeCollection = async (e, details, id) => {
        const event = e;
        if (event.key === 'Enter') {
            let updated_balance = parseInt(details.ammount_Balance) - parseInt(collectedammout)
            let records = {
                name: details.name,
                loanammount: details.loanammount,
                loan_date: details.loan_date,
                loanInterst: details.loanInterst,
                ms: details.ms,
                place: details.place,
                ammount_Balance: updated_balance,
                place_id: details.place_id
            };
            let collection = {
                date: date1,
                costumer_id: details.id,
                costumer_name: details.name,
                ammount: collectedammout
            };
            if (parseInt(collectedammout) !== 0)  {
                    axios.post(createCollectionList_Server, collection).then((res) => {
                        axios.post(`${updateCostumerDetails_Server}${details.id}/`, records)
                            .then((res) => {
                                console.log("updated")
                            }, (err) => { console.log('error put', id) });
                    }, ((error) => { console.log(error, "error") }));
            }

            const nextIndex = id+1;
            inputRefs.current[nextIndex] && inputRefs.current[nextIndex].focus();
        };
    };


    const getCostumerDetails = async () => {
        axios.get(CostumerDetails_Server).then((res) => {
            setCostumerDetails(res.data)
        });
    };

    const getCurrentDate = async() => {
        let date1 = new Date()
        let month = date1.getMonth()
        let date = date1.getDate()
        if (month < 10) {
            month = parseInt(month) + 1
            month = "0" + month
        }
        if (date < 10) {
            date = parseInt(date) 
            date = "0" + date
        }
        let prevDate = date1.getFullYear() + "-" + (month) + "-" + date 
        await setDate1(prevDate);
        localStorage.setItem("date", prevDate)
    }

    const searchCostumerData = async () => {
        let collections;
        await axios.get(CostumerDetails_Server).then((res) => {
            return collections = res.data
        })
        var searchdata =  await collections.filter((collectionlist) => 
                                    collectionlist.name.includes(searchTerm)
        )
        setCostumerDetails(searchdata)
    }

    useEffect(() => {
        setLoadingState(true)
        getCostumerDetails();
        getCurrentDate();
    }, [])


     const handleFocus = async (e, id) => {
        await setCollectedammount(e.target.value)
        // await getprevcollection(id)
        e.target.select();
    };

    
    // function getprevcollection(costumerid) {
    //     let collectionlist = [];
    //     let ammount = ''
    //     axios.get(`${CollectionListDetail_Server}${costumerid}/`).then((res) => {
    //         return console.table(res.data)
    //     })
    //     collectionlist.map((data) => {
    //         collectionlist = data
    //     })
    //     console.log(ammount)   
    // }

   
    let date;

    function addDays(date) {
        let now = new Date()
        const copy = new Date(Number(date))
        copy.setDate(date.getDate() + 100)
        var copyDate = new Date(copy)
        if (now > copyDate) {
            return true
        }
        return false
    };

    var copyDate1 = '';

    function subDays() {
        let now = new Date()
        let copy = new Date(Number(now))
        copy.setDate(now.getDate() - 1)
        copyDate1 = new Date(copy)
    };

    let day0 = '';


    return (
        <div className={"addCollection_"}>
            <div className={"addCollection__dateInput"}>
                <p>Date</p>
                <TextField
                    type="date"
                    name="collection date"
                    onChange={changeDate}
                    InputLabelProps={{
                        shrink: true,
                    }}
                    value = {date1}
                />
                <input type="text" id="outlined-search"
                    style={{
                        marginLeft: '20px',
                        outline: 'none',
                        flex: '0.9'
                    }}
                    onChange={e => setSearchTerm(e.target.value)} placeholder="Enter Costumer Name"
                    className={"collectionDetails__input"}
                />
                <Button variant="contained" color="secondary" onClick={searchCostumerData}>🔍 Search</Button>
            </div>
            <div>
                <div className={"addCollection__collectionTable"}>
                    
                    {
                        loadingState ? (
                                costumerDetails
                                .sort((a, b) => {
                                if (a.place_id < b.place_id) return -1;
                                if (a.place_id > b.place_id) return 1;
                                return 0;
                                })
                                    .map((details, id) => {
                                    date = new Date(details.loan_date)
                                        date = addDays(date)
                                        return (
                                            <div className={"addCollection__collectionTableBody"} key={id}>
                                                <p
                                                className={date === false ? 'normal' : 'late'}
                                                    >{details.name}</p>
                                                <p>{details.loanammount}</p>
                                                <p>{details.ammount_Balance}</p>
                                                
                                                    <TextField
                                                        label="Ammount"
                                                        inputProps = {{onKeyPress:(e) => changeCollection(e, details, id)}}
                                                        onChange={changevalue}
                                                        inputRef={(ref) => (inputRefs.current[id] = ref)}
                                                        onFocus={
                                                            e => handleFocus(e, details.id)
                                                        }
                                                        color="secondary"
                                                        style={{
                                                            "margin" : "10px"
                                                        }}
                                                        defaultValue={details.loanammount / 100}
                                                />
                                                <div>
                                                    {collectionStateData}
                                                </div>
                                            </div>
                                    )
                                    })
                    ) : (
                            <p className="loadingstate">Loading Data....</p>
                        )
                    }
            </div>
            
            </div>
        </div>
    )
}
