import React, { Component } from 'react'
import LOGO from './LOGO.jpg'
import './settings.css'

export default class Settings extends Component {
    constructor(props) {
        super(props);
        this.state = {
            company: ""
        }
    }
    setCompany = () => {
        localStorage.setItem("company", this.state.company)
    };

    componentDidMount() {
        const company = localStorage.getItem("company")
        this.setState({
            company
        })
    }
    
    render() {
        return (
            <div className="settings__">
                <img src={LOGO} alt={"LogoImage"} className="settings__LOGO" />
                <div className="contents">
                    <input type="text" onChange={e => this.setState({
                        company: e.target.value
                    })} placeholder="Enter Company Name" />
                    <button
                        onClick={this.setCompany}
                        className="collectionDetails__button">Save Company</button>
                    <br />
                    <br />
                    <br />
                    <h2>Software Name: { this.state.company }</h2>
                    <h2>Software Version: 1.0.0</h2>
                    <h2>Software By: ABIPRAVI</h2>
                    
                </div>
            </div>
        )
    }
}
