import React from 'react';
import axios from 'axios';
import "./addcostumer.css";

import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';

import {
  Link,
} from "react-router-dom";

const authKey = sessionStorage.getItem("access_token")
const proxy = "http://localhost:8000/"
const createCostumer_server = `${proxy}createcostumer/${authKey}/`

export default class addCostumer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            costumerInput: {
                name: "",
                loanammount: 0,
                loan_date: null,
                loanInterst: 0,
                ms: 0,
                place: "",
                ammount_Balance: 0,
                place_id: 0,
            },
        }
    }


    onchangeText1 = (e) => {
        this.setState({
            CostumerInput: {
                ...this.state.CostumerInput,
                [e.target.name]: e.target.value
            }
        })
        }

    setStateValue = () => {
        this.savecostumerData(this.state.CostumerInput)  
    }

    savecostumerData = async(UserData) => {
        axios.post(createCostumer_server,UserData).then((result) => {
            alert('User Added Successfully..')
        }, (error) => {
            console.log(error, 'error')
        })
    }

    render() {
            return (
            <div className="body">
                <div className="addcostumer__inputs">
                        <input type="text"  onChange={this.onchangeText1}  placeholder="Costumer Name" name="name" />
                        <input type="number"  onChange={this.onchangeText1}  placeholder="Loan Ammount" name="loanammount" />
                        <input type="number"  onChange={this.onchangeText1}  placeholder="Ammount Balance" name="ammount_Balance" />
                        <TextField
                            type="date"
                            name="loan_date"
                            defaultValue={this.state.date}
                            InputLabelProps={{
                                shrink: true,
                            }}
                            onChange={this.onchangeText1}
                        />
                        <input type="text"  onChange={this.onchangeText1}  placeholder="Loan Intrest" name="loanInterst" />
                        <input type="text"  onChange={this.onchangeText1}  placeholder="Loan MS" name="ms" />
                        <input type="text" onChange={this.onchangeText1} placeholder="Place" name="place" />
                        <input type="text"  onChange={this.onchangeText1}  placeholder="Place Id" name="place_id" />
                        <Button type="button" color="secondary" variant="contained" style={{"margin" : 10}} onClick={this.setStateValue}>Save Costumer Data</Button>
                        <Link
                            style={{ "margin": 10, "border": "solid 1px #f50057", "color": "white", "padding": 3, "boxShadow": "0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%)", "letterSpacing": "0.02857em", "lineHeight": 1.74, "background" : "#f50057", "textTransform" : "uppercase", "borderRadius" : 5, "textDecoration" : "none" }}
                            to="/updateCostumer">
                            Update Costumers
                        </Link>
                </div>
            </div>
        )
        }
}
