import React, { useState, useEffecte } from 'react';
import axios from 'axios'
import './plan.css'

const proxy = "http://localhost:8000/"

const authKey = sessionStorage.getItem('access_token')
const COSTUMER_DETAILS_SERVER = `${proxy}getcostumer/${authKey}/`
const COSTUMERCLOSEHIGH = `${proxy}createcloseup`
const COSTUMERCLOSELOW = `${proxy}createclosedown`
const other_ammount_inServer = `${proxy}createoai`
const inversmentServer = `${proxy}createinversment`
const other_ammount_outServer = `${proxy}createoao`
const othersServer = `${proxy}createothers`

export class Plan extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			loanTotal: 0,
			loanIntrestTotal: 0,
			MStotal: 0,
			entryName: '',
			entryName1: "",
			entryAmmount: '',
			showField: false,
			showField1: false,
			closeCostumerName: [],
			lineup: '',
			linedown: "",
		}
	}

	loadData = async() => {
		this.get_Loan_Total()
		this.get_Loan_Intrest()
		this.get_Loan_MS()
	}

	get_Loan_Total = async() => {
		let loanData;
		let loanTotal = 0;
		await axios.get(COSTUMER_DETAILS_SERVER).then((res) => {
			loanData = res.data.filter((data) => data.loan_date.includes(this.props.date))
		})
		await loanData.map((data1) => {
			return loanTotal = parseInt(loanTotal) + parseInt(data1.loanammount)
		})
		await this.setState({
			loanTotal
		})
		localStorage.setItem("loan", loanTotal)
	}

	get_Loan_Intrest = async() => {
		let loanData;
		let loanIntrestTotal = 0;
		await axios.get(COSTUMER_DETAILS_SERVER).then((res) => {
			loanData = res.data.filter((data) => data.loan_date.includes(this.props.date))
		})
		await loanData.map((data1) => {
			return loanIntrestTotal = parseInt(loanIntrestTotal) + parseInt(data1.loanInterst)
		})
		await this.setState({
			loanIntrestTotal
		})
		localStorage.setItem("interst", loanIntrestTotal)
	}

	changeOption = async(e) => {
		await this.setState({
			entryName: e.target.value
		})
		this.setNameField()
	}
	changeOption1 = async(e) => {
		await this.setState({
			entryName1: e.target.value
		})
		this.setNameField1()
	}

	get_Loan_MS = async() => {
		let loanData;
		let MStotal = 0;
		await axios.get(COSTUMER_DETAILS_SERVER).then((res) => {
			loanData = res.data.filter((data) => data.loan_date.includes(this.props.date))
		})
		await loanData.map((data1) => {
			return MStotal = parseInt(MStotal) + parseInt(data1.ms)
		})
		await this.setState({
			MStotal
		})
		localStorage.setItem("ms", MStotal)

	}

	postEntryin = async () => {
		let date = this.props.date
		const { entryName, entryAmmount, lineup } = this.state;

		if (entryName === 'Costumer Excess') {
			let postCostumerdata = {
				date: date,
				costumername: lineup,
				ammount: entryAmmount
			}
			axios.post(COSTUMERCLOSEHIGH, postCostumerdata)
		}

		else if (entryName === 'Ammount Excess') {
			let postData = {
				date: date,
				reason: entryName,
				ammount: entryAmmount
			}
			axios.post(`${other_ammount_inServer}`, postData)
		}
		else if (entryName === 'Add Cash') {
			let postData = {
				date: date,
				reason: "Add Ammount",
				ammount: entryAmmount
			}
			axios.post(`${other_ammount_inServer}`, postData)
		}

		else if (entryName === 'Inversment') {
			let postData = {
				date: date,
				name:lineup,
				ammount: entryAmmount
			}
			axios.post(`${inversmentServer}`, postData)
		}
		this.setState({
			loneCloseCostumername: '',
			entryAmmount: '',
		})
		window.location.reload()
	}

	postEntryout = async () => {
		let date = this.props.date
		const { entryName1, entryAmmount, linedown } = this.state;

		if (entryName1 === 'Costumer less ammount') {
			let postCostumerdata = {
				date: date,
				costumername: linedown,
				ammount: entryAmmount
			}
			axios.post(COSTUMERCLOSELOW,postCostumerdata)
		}
		else if (entryName1 === 'Ammount Less') {
			let postData = {
				date: date,
				reason: entryName1,
				ammount: entryAmmount
			}
			axios.post(`${other_ammount_outServer}`, postData)
		}
		else if (entryName1 === 'Profit takeout / Salary') {
			let postData = {
				date: date,
				reason:linedown,
				ammount: entryAmmount
			}
			axios.post(`${othersServer}`, postData)
		}
		this.setState({
			linedown: '',
			entryAmmount: '',
		})
		window.location.reload()
	}

	setNameField = async () => {
			this.setState({
				showField: true
			})
	}

	setNameField1 = async () => {
			this.setState({
				showField1: true
			})
	}

	componentDidMount() {
		this.loadData();

	}

	render() {
		return (
			<div className="plan__">
				<div className="plan__leftarea">
					<button onClick={this.loadData}
				className="plan__button"
				>
					Reload Data
				</button>
				<div className='plan__area'>
					<div className="plan__area__items">
						Total Loan: <h5>{this.state.loanTotal}</h5>
					</div>
					<div className="plan__area__items">
						Loan Intrest: <h5>{this.state.loanIntrestTotal}</h5>
					</div>
					<div className="plan__area__items">
						Loan MS: <h5>{this.state.MStotal}</h5>
					</div>
				</div>
					<div style={{'display' : 'flex'}}>
						<div className="plan__addItems">
							<select name="inputMaster" className="plan__choise" onChange={this.changeOption}>
								<option>Ammount In</option>
								<option>Costumer Excess</option>
								<option>Ammount Excess</option>
								<option>Inversment</option>
								<option>Add Cash</option>
							</select>
								{
									this.state.showField ? (
										<div>
											<input type="text" className="plan__showField" placeholder="Enter  Name"
											onChange={e =>
												this.setState({
													lineup: e.target.value
												})
											}
											value = {this.state.lineup}
										>
											</input>
										</div>
									) : (<br /> )
								}
								<input type="text"
								onChange={e => this.setState({ entryAmmount: e.target.value })} placeholder="Enter Ammount" value = {this.state.entryAmmount} />
								<br />
								<button className="plan__button" onClick={this.postEntryin}>Add</button>
						</div>
						<div className="plan__addItems1">
							<select name="inputMaster" className="plan__choise" onChange={this.changeOption1}>
								<option>Ammount Out</option>
								<option>Costumer less ammount</option>
								<option>Ammount Less</option>
								<option>Profit takeout / Salary</option>
							</select>
							{
								this.state.showField1 ? (
									<div>
										<input type="text" className="plan__showField" placeholder="Enter  Name"
										onChange={e =>
										this.setState({
											linedown: e.target.value
										})
										}
										value = {this.state.linedown}
										>
										</input>
									</div>
								) : (<br /> )
							}
							<input type="text"
							onChange={e => this.setState({ entryAmmount: e.target.value })} placeholder="Enter Ammount" value = {this.state.entryAmmount} />
							<br />
							<button className="plan__button" onClick={this.postEntryout}>Add</button>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Plan
